import moment from 'moment';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { NAO_RETIFICADO, RETIFICACAO_ENUM_VALUES, RETIFICADO } from '../../../../../models/enumerated/mfp/retificacaoEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import EmpresaIS from '../../../../components/ger/empresaIS/EmpresaIS';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { AnaliseRaisRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './analiseRaisRV';

const DEFAULT_VALUES: AnaliseRaisRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  empresas: [],
  gerarCabecalhoPadraoAdministradora: true,
  retificacao: NAO_RETIFICADO.value,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: false,
};

const AnaliseRaisReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<AnaliseRaisRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const [isEnabledtRetificacao, setIsEnabledtRetificacao] = useState(true);
  const [isEnableCrea, setIsEnableCrea] = useState(true);

  const handleOnchangeRetificacao = () => {
    if (formProps.getValues().retificacao === NAO_RETIFICADO.value) {
      setIsEnabledtRetificacao(true);
      setIsEnableCrea(true);
    } else if (formProps.getValues().retificacao === RETIFICADO.value) {
      setIsEnabledtRetificacao(false);
      setIsEnableCrea(false);
    }
  };

  useEffect(
    () => {
      setTimeout(() => {
        if (globalReducer.globalParameter.empresa !== null && globalReducer.globalParameter.empresaMFP !== null) {
          const ano = moment(globalReducer.globalParameter.empresaMFP.referencia, 'YYYY-MM');
          formProps.setValue('ano', ano.format('YYYY'));
        }
      }, 800);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      title="Análise RAIS"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <EmpresaIS
          id="empresas"
          formProps={{ ...formProps, validation: { required: true } }}
          inputSearchProps={{ selectProps: { isMulti: true }, validation: { required: true } }}
          placeholder="Informe a Empresa"
          isPrependLabel={true}
          label="Empresa"
        />

        <VinculoIS
          id="responsavel"
          placeholder="Informe o Responsável"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Responsável"
          isPrependLabel={true}
        />

        <InputGroupInline
          id="retificacao"
          label="Retificação"
          isPrependLabel={true}
          placeholder="Informe a Retificação"
          formProps={formProps}
          inputDropDownProps={{
            optionsProvider: RETIFICACAO_ENUM_VALUES,
            selectProps: { isClearable: true },
          }}
          type={InputType.DROP_DOWN}
          onChange={handleOnchangeRetificacao}
        />

        <InputGroupInline
          disabled={isEnabledtRetificacao}
          id="dtRetificacao"
          formProps={formProps}
          isPrependLabel={true}
          placeholder="Informe a Data de Retificação"
          label="Data de Retificação"
          type={InputType.DATE}
        />

        <InputGroupInline disabled={isEnableCrea} placeholder="Informe o CREA" id="crea" formProps={formProps} isPrependLabel={true} label="CREA" />

        <InputGroupInline
          id="ano"
          formProps={{ ...formProps, validation: { min: 1930, max: 3000 } }}
          isPrependLabel={true}
          label="Ano Base"
          placeholder="Informe o Ano Base"
          type={InputType.NUMBER}
        />
      </ReportForm>
    </WindowComponent>
  );
};

export default AnaliseRaisReportView;
